$mainColor: white;
.pages{
    max-width: 100vw;
    min-width: 100vw;
    min-height: 100vh; 
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: relative;
    background-color: #201d37;
    display: flex;
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    resize: both;
    color:white;
    /* visibility: hidden; */

    .link{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        flex-direction: row;
        width: 100%;
        .containermails{
            flex: 0 0 50%;
            max-width: 50%;
        }
        span{
            font-size: 30px;
            margin: 5%;
        }
        a{
            margin:5%
        }
        
    }
    @media (max-width: "767px"){
        .row{
            flex-direction: column;
            .containermails{
                max-width: 100%;
            }
        }
    }
}